.loader{display:flex;justify-content:center;align-items:center;height:100vh;margin:0}.photo-details{display:flex;flex-direction:column;align-items:center;justify-content:center;height:90vh;background-color:rgba(245,245,245,.1098039216);padding:55px 20px 0 20px;box-sizing:border-box}h3{font-size:24px;margin-bottom:20px}.photo-container{display:flex;align-items:center;justify-content:center;flex-wrap:wrap;margin:0 -10px}.button-container{display:flex;justify-content:space-between;align-items:center;width:100%;margin-bottom:20px}.arrow-btn,.fa-xmark{padding:15px 12px;cursor:pointer;font-size:2rem}.details-pic{max-width:50rem;min-width:50rem;max-height:71vh;width:auto;height:auto;display:block;margin:0 .5rem}.fa-xmark{position:absolute;top:0;right:10px}@media screen and (max-width: 885px){.details-pic{max-width:39rem;min-width:39rem}.arrow-btn{padding:10px 10px;cursor:pointer;font-size:1rem}.fa-xmark{font-size:1rem}}@media screen and (max-width: 750px){.details-pic{max-width:30rem;min-width:30rem}.arrow-btn{padding:20px 2px;cursor:pointer;font-size:1rem}.fa-xmark{font-size:1rem}}@media screen and (max-width: 600px){.details-pic{max-width:20rem;min-width:10rem}.arrow-btn{display:none;padding:20px 2px;cursor:pointer;font-size:1rem}.fa-xmark{font-size:1rem}}.photo-container{display:flex;align-items:center;justify-content:center;flex-wrap:wrap;margin:0 -10px;transition:opacity .5s ease-in-out}.photo-item{flex:1;padding:0 10px;opacity:0}.active-photo{opacity:1}.fa-chevron-right:before{position:absolute;content:"";right:2%}.fa-chevron-left:before{position:absolute;content:"";left:2%}